import request from './request'

export const getCateList = () => {
  return request({
    url: 'categories',
    method: 'get'
  })
}

export const addCate = (data) => {
  return request({
    url: 'categories',
    method: 'post',
    data
  })
}

export const getOneCate = (id) => {
  return request({
    url: `categories/${id}`,
    method: 'get'
  })
}

export const editCate = (id, name) => {
  return request({
    url: `categories/${id}`,
    method: 'put',
    data: { cat_name: name }
  })
}
export const deleteCate = (id) => {
  return request({
    url: `categories/${id}`,
    method: 'delete'
  })
}
